* {
	margin: 0;
	padding: 0;
}

.contener {
	height: 100%;
	width: 100%;
	background-color: rgba(2, 225, 139, 1.0);
	background-position: center;
	background-size: cover;
	position: absolute;
}

.main {
	width: 380px;
	height: 500px;
	position: relative;
	margin: 5% auto;
	background: #666;
	box-shadow: 5px 5px 20px 1px #666;
	padding: 15px 2px 0 5px;
	border-radius: 15px;
	overflow: hidden;
}
/*form*/

.google_box {
	text-align: center;
	margin-top: -20px;
}

.btn_style {
	margin-top: 20px;
	font-weight: 500;
	color: rgba(2, 225, 139, 1.0);
}

.btn-light:hover {
	background-color: rgba(2, 225, 139, 0.9);
	border-color: rgba(2, 225, 139, 0.5);
}

.signout {
	position: fixed;
	top: 5px;
	right: 5px;
}


main {
	position: relative;
	height: 70vh;
	overflow-y: scroll;
	padding:0 5px 20px 5px;
}

main::-webkit-scrollbar {
	width: 0.25rem;
}

main::-webkit-scrollbar-track {
	background: rgb(44, 63, 55);
}

main::-webkit-scrollbar-thumb {
	background: rgb(2, 225, 139);
}

form {
	height: 50px;
	position: absolute;
	bottom: 10px;
	left: 0;
	width: 100%;
	max-width: 728px;
	display: flex;
	font-size: 1.5rem;
}

form button {
	width: 20%;
	background-color: rgb(2, 225, 139);
}


input {
	line-height: 1;
	width: 100%;
	font-size: 1rem;
	background: rgb(58, 58, 58);
	color: white;
	outline: none;
	border: none;
	padding: 0 10px;
	border-top-left-radius: 20px;
	border-bottom-left-radius: 20px;
	margin-left: 10px;
}

button {
	background-color: #282c34;
	/* Green */
	border: none;
	color: white;
	padding: 5px 10px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	cursor: pointer;
	font-size: 1.25rem;
	border-top-right-radius: 20px;
	border-bottom-right-radius: 20px;
	margin-right: 10px;
}

button:disabled {
	/* opacity: 0.5; */
	background-color: rgb(2, 225, 139);
	cursor: not-allowed;
}

ul,
li {
	text-align: left;
	list-style: none;
}

p {
	max-width: 500px;
	margin-bottom: 4px;
	line-height: 20px;
	padding: 8px 20px;
	border-radius: 15px;
	position: relative;
	color: white;
	text-align: center;
}

.message {
	display: flex;
	align-items: center;
}


.sent {
	flex-direction: row-reverse;
}

.sent p {
	color: white;
	background: rgba(2, 225, 139, 0.884);
	align-self: flex-end;
}

.received p {
	background: #e5e5eaf3;
	color: black;
}

.dp {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	margin: 2px 5px;
}
.send{
	width: 30px;
	height: 30px;
	margin: 5px 8px;

}